<template>
    <div class="boxes">
    </div>
</template>
  
<style scoped>
.boxes {
  background-color: rgb(12, 12, 66);
}
</style>

<script setup>
import { useRouter } from 'vue-router'
const router = useRouter()
router.beforeEach(function(to, from, next){
  if(to.path == '/2') {
    to.meta.transition = 'slide-left'
    from.meta.transition = 'slide'
  }
  else {
    to.meta.transition = 'slide'
  }
  window.removeEventListener("touchstart",startevent);
  window.removeEventListener("touchend", endevent);
  next();
}); 

function startevent(event) {
  if(event.touches.length === 1){
    start = event.touches.item(0).clientX;
  }else{
    start = null;
  }
}

function endevent(event) {
  var offset = 50;
  if(start){
    var end = event.changedTouches.item(0).clientX;
    if(end > start + offset){
      console.log('prev');
      router.push('/2');
    }
    else if(end < start - offset ){
      console.log('next');
      router.push('/main/01');
    }
  }
}

var start = null;
window.addEventListener("touchstart",startevent);
window.addEventListener("touchend", endevent);
</script>